import React, { Component } from 'react';
import TableBlock from '../components/TableBlock.js'
import { connect } from "react-redux"
import Header from '../components/Header.js'
import checkAuthAndRefresh from '../dbActions/checkAuth'
import Loading from './loading'
import {Button, ButtonGroup} from 'react-bootstrap'

class Assigned_tasks extends Component {
    constructor(props){
        super(props);
        this.state = {
            tasksCardView: false,
            isListActive: true
        }
    }

    componentWillMount() {
        checkAuthAndRefresh(this.props.dispatch)
    }

    render() {
        if (!this.props.authChecked || !this.props.isLoggedIn) {
            return (<Loading />)
        }

        return (
            <div>
                <Header /><div className='app'>
                <div className="wrapper d-flex justify-content-center">
                <main className="m-2">
                    <div className="mb-4 mt-4 d-flex flex-row">
                        <h2><img src='/static/assigned.png' width="60"/>איסופים קרובים</h2>
                    </div>
                    <ButtonGroup aria-label="Basic example">
                    <Button variant={this.state.isListActive ? 'primary' : 'secondary'}><i class="fas fa-list"></i></Button>
                    <Button variant={this.state.isListActive ? 'secondary' : 'primary'}><i class="far fa-clone"></i></Button>
                    </ButtonGroup>
                    <TableBlock tasksCardView={this.state.taskCardView} page='assignedTasks' type='tasks' data={this.props.assignedTasks} />
                </main></div></div>
            </div>
        )
    }
}

export default connect(state => state)(Assigned_tasks);